/******************
 * Type Selectors *
 ******************/

:root {
  /* Theme colors */
  --primary-color: #D09B2C;
  --tint-color-end: #D09B2C70;
  --tint-color-start: #770000D0;
  --bg-color-light: #ECE2E2;
  --bg-color-dark: black;
  --font-color-light: white;
  --font-color-dark: #313338;

  /* Font stacks */
  --text-font: 'Open Sans', sans-serif;
  --header-font: 'Montserrat', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: white;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--header-font);
  font-size: inherit;
}

.request {
	padding: 10px;
	color: white;
	background: #4747d6;
	border-radius: 4px;
	width: auto;
}

button {
	border: solid 2px;
	padding: 5px;
	width: 70px;
	background: #d09b2c;
	margin-right: 10px;
}
/*
 * Block
 */

.navbar {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 96vw;
  padding: 0.2em 2vw;
  background-color: var(--bg-color-dark);
  color: var(--font-color-light);
  font-size: 24pt;
}

/*
 * Element
 */

.navbar__logo {
  align-self: flex-start;
}

.navbar__menu {
  text-align: right;
}

.navbar__list {
  font: bolder 0.5em var(--text-font);
  display: inline-flex;
  color: inherit;
}

.navbar__list-item {
  margin: 0.3em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/*
 * Modifier
 */

.navbar__list--dropdown {
  display: block;
}

/*
 * Block
 */

.text {
  font-family: var(--text-font);
  font-size: 12pt;
  color: inherit;
}

.text:first-of-type {
  margin-top: 0;
}

.text:last-of-type {
  margin-bottom: 0;
}

/*
 * Modifier
 */

.text--accent {
  color: var(--primary-color);
}

.text--prefix {
  margin-right: 0.5em;
}

.text--big {
  font-weight: bold;
  font-size: 15pt;
}

.text--bigger {
  font-weight: bold;
  font-size: 24pt;
}

/*
 * Block
 */

.clickable {
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  color: unset;
}

/*
 * Modifier
 */

.clickable--border {
  border: solid 2px;
  padding: 0.3em;
}

.clickable--nav-link {
  display: block;
  font-family: var(--text-font);
  text-transform: uppercase;
}

.clickable--nav-link:hover,
.clickable--border:hover {
  color: var(--primary-color);
}

.clickable--link {
  text-decoration: underline;
}

.clickable--button {
  color: inherit;
}

/*
 * Block
 */

.section {
  margin: 2.4em 0;
  padding: 0 10%;
}

.section:first-of-type {
  margin-top: 0;
}

.section:last-of-type {
  margin-bottom: 0;
}

/*
 * Element
 */

.section__header {
  position: relative;
  left: -3%;
  font-size: 36pt;
  display: inline-block;
  margin-bottom: 1em;
  border-bottom: 0.1em solid var(--primary-color);
}

@media (max-width: 510px) {
  .section__header {
    font-size: 24pt;
  }
}

/*
 * Modifier
 */

.section--main {
  font-size: 64pt;
  margin: 0;
  text-align: center;
  min-height: 100vh;
  background-image:
    linear-gradient(
      var(--tint-color-start),
      var(--tint-color-end)),
    url(/dragonhacks/static/media/dragon_arcade.9bbcd361.png);
  background-position: center;
  background-size: cover;
  color: var(--font-color-light);
}

.section--main::before {
  content: "";
  padding-top: 20vh;
  display: block;
}

.section--main::after {
  content: "";
  padding-bottom: 16px;
  display: block;
}

/*
 * Block
 */

.logo {
  display: block;
}

/*
 * Element
 */

.logo__image {
  width: 200px;
}

/*
 * Block
 */

.sponsor-tier {
  margin: 40px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}

/*
 * Element
 */

/* Broke BEM guidelines for sanity */
.sponsor-tier > * {
  margin: 30px;
}

/*
 * Block
 */

.faq {
  margin: 2em 0;
}

.faq:first-of-type {
  margin-top: 0;
}

/*
 * Element
 */

.faq__question {
  font-size: 18pt;
  margin-bottom: 0.2em;
}

@media (max-width: 510px) {
  .faq__question {
    font-size: 14pt;
  }
}

